import {useState} from "react";
import headshot from "./image/man-image.jpg";
import './App.css';
import Button from "./Component/Button"
import { FaTwitter } from "react-icons/fa";
import { FaLinkedinIn } from "react-icons/fa";
import { FaInstagram } from "react-icons/fa";

function App() {
  const [data, setData] = useState("Cena")  
  const [count, setCount] = useState(0)  
  const handleSkill = () => {
    setData("I am a web Developer")
  }

  const handleName = () => {
    setData("My name is Cena")
  }

  const handleCount = () => {
    setCount(count + 1)
  }


  
  // DRY Don't Repeat Yourselt
  // DIE Duplicate Is Evil
  
  return (
    <div className="App">
      <main>
      <div className="wrapper">
      <div>
        <img src={headshot} alt="Pic"/>
        <h1>{data}</h1>
      </div>
      <div className="buttons">
        <Button handleClick={handleName} className="name" btnTitle="Name"/>
        <Button handleClick={handleSkill} className="skills" btnTitle="Skills" />
      </div>
      <div className="icons">
        <a href="https://twitter.com/codinghag"><FaTwitter /></a>
        <a href="https://www.linkedin.com/in/cena-s-208b2ba/"><FaLinkedinIn /></a>
        <a href="https://www.instagram.com/codinghag/"><FaInstagram /></a>
      </div>
      <div className="likes">

        <Button handleClick={handleCount}  btnTitle="Like Me?" />
      </div>
      <div >
        <p>{count}</p>
      </div>
    </div>
      </main>
    </div>
  );
}

export default App;
